import React, { Component, ComponentType } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import {
  Chip,
  createStyles,
  Grid,
  Theme,
  Typography,
  withStyles,
  WithStyles,
} from "@material-ui/core";

import { AppState } from "../../../_types";
import { AppActions } from "../../../_types";
import { FavoriteCard } from "./FavoriteCard";
import { LoadingCircle } from "../../Layout/Loadings";
import { IFavorite } from "../../../_types/profile.types";
import { profileRequest } from "../../../_actions/profile.actions";

const useStyles = (theme: Theme) =>
  createStyles({
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 240px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: 0,
    },
    autoMargin: {
      margin: "auto",
    },
  });

interface FavoriteProps extends WithStyles<typeof useStyles> {
  favorite?: IFavorite[];
  error: string;
  loading: boolean;
}
type Props = FavoriteProps & LinkDispatchProps;

class FavoriteList extends Component<Props> {
  shouldComponentUpdate(nextProps: Props) {
    if (!nextProps.loading) {
      return true;
    }
    return false;
  }
  componentDidMount() {
    if (this.props.loading) this.props.profileRequest();
  }

  render() {
    const { loading, favorite, error, classes } = this.props;
    const length = favorite ? favorite.length : 0;
    return (
      <>
        <div className={classes.header}>
          <Typography variant="h3">موردعلاقه ها</Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={"تعداد: " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <LoadingCircle />
          ) : favorite ? (
            favorite
              .slice(0)
              .reverse()
              .map((favorite: IFavorite) => (
                <FavoriteCard key={favorite.id} favorite={favorite} />
              ))
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
        </Grid>
      </>
    );
  }
}

interface LinkDispatchProps {
  profileRequest: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): FavoriteProps => {
  return {
    classes: ownProps.classes,
    ...state.user,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  profileRequest: bindActionCreators(profileRequest, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(FavoriteList)) as unknown) as ComponentType<unknown>;
