import React, { useState, useEffect } from "react";
import { push } from "connected-react-router";
import { connect } from "react-redux";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import {
  Tab,
  Box,
  Tabs,
  AppBar,
  makeStyles,
  Paper,
  Theme,
  useMediaQuery,
} from "@material-ui/core";
import {
  LibraryMusic,
  QueueMusic,
  MusicNote,
  EmojiSymbols,
  Favorite,
} from "@material-ui/icons";

import LearningHeader from "./LearningHeader";
import FavoriteList from "./Favorite/FavoriteList";
import { AppActions, AppState, BankType } from "../../_types";
import LearningBody from "./LearningBody";

export interface IPage {
  title: string;
  link: string;
  type: string;
  name: keyof Omit<BankType, "posts">;
  icon: JSX.Element;
  label: string;
}

interface IP {
  title: string;
  link: string;
  type: string;
  name: keyof Omit<BankType, "posts"> | "favorites";
  icon: JSX.Element;
  label: string;
}
const pages: IP[] = [
  {
    title: "مجموعه ها",
    type: "guide",
    link: "/guide",
    name: "guides",
    icon: <LibraryMusic />,
    label: "GUIDES",
  },
  {
    title: "آموزش ها",
    type: "course",
    link: "/course",
    name: "courses",
    icon: <QueueMusic />,
    label: "COURSES",
  },
  {
    title: "درس ها",
    type: "lesson",
    link: "/lesson",
    name: "lessons",
    icon: <MusicNote />,
    label: "LESSONS",
  },
  {
    title: "تمرین ها",
    type: "exercise",
    link: "/exercise",
    name: "exercises",
    icon: <EmojiSymbols />,
    label: "EXERCISES",
  },
  {
    title: "موردعلاقه ها",
    type: "favorite",
    link: "/favorite",
    name: "favorites",
    icon: <Favorite />,
    label: "FAVORITES",
  },
];

const pageName = (num: number) => {
  return pages[num].link;
};
const pageNumber = (name: string) => {
  const obj = pages.find((page) => page.link === name);
  if (obj) return pages.indexOf(obj);
  return 0;
};

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: "100vw",
  },
  tabControl: {
    [theme.breakpoints.down(880)]: {
      minWidth: 150,
    },
    [theme.breakpoints.down(830)]: {
      minWidth: 140,
    },
    [theme.breakpoints.down(780)]: {
      minWidth: 120,
    },
    [theme.breakpoints.down(680)]: {
      minWidth: 100,
    },
    [theme.breakpoints.down(500)]: {
      minWidth: 80,
    },
    [theme.breakpoints.down(370)]: {
      minWidth: 65,
    },
    [theme.breakpoints.down(320)]: {
      minWidth: 55,
    },
  },
  tabPanel: {
    padding: theme.spacing(2),
  },
}));

interface Props {
  loggedIn: boolean;
  pathName: string;
  push: (num: string) => void;
}

const LearningPage: React.FC<Props> = ({ loggedIn, pathName, push }) => {
  const classes = useStyles();

  const [value, setValue] = useState(pageNumber(pathName));

  useEffect(() => {
    if (pathName === "/favorite" && !loggedIn) push(pageName(0));
    else setValue(pageNumber(pathName));
  }, [pathName, loggedIn, push]);

  const matches = useMediaQuery("(min-width:500px)");

  const handleChange = (newValue: number) => {
    setValue(newValue);
    push(pageName(newValue));
  };
  return (
    <>
      <AppBar position="static">
        <Paper square>
          <Tabs
            value={value}
            onChange={(_, e) => handleChange(e)}
            variant="scrollable"
            indicatorColor="secondary"
            textColor="secondary"
            scrollButtons="desktop"
            aria-label="icon label tabs"
          >
            {pages.map((page, i) =>
              i !== 4 ? (
                <Tab
                  className={classes.tabControl}
                  key={page.label}
                  icon={page.icon}
                  label={matches ? page.label : null}
                  id={`full-width-tab-${i}`}
                  aria-controls={`full-width-tabpanel-${i}`}
                />
              ) : (
                loggedIn && (
                  <Tab
                    className={classes.tabControl}
                    key={page.label}
                    icon={page.icon}
                    label={matches ? page.label : null}
                    id={`full-width-tab-${i}`}
                    aria-controls={`full-width-tabpanel-${i}`}
                  />
                )
              )
            )}
          </Tabs>
        </Paper>
      </AppBar>
      {pages.map((page, i) => {
        return (
          <div
            key={page.label + i}
            role="tabpanel"
            hidden={value !== i}
            id={`full-width-tabpanel-${i}`}
            aria-labelledby={`full-width-tab-${i}`}
          >
            {value === i && (
              <Box className={classes.tabPanel}>
                {page.name !== "favorites" ? (
                  <>
                    <LearningHeader page={page as IPage} />
                    <LearningBody page={page as IPage} />
                  </>
                ) : (
                  <FavoriteList />
                )}
              </Box>
            )}
          </div>
        );
      })}
    </>
  );
};
const mapStateToProps = (state: AppState) => ({
  loggedIn: state.auth.loggedIn,
  pathName: state.router.location.pathname,
});
const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  push: bindActionCreators(push, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LearningPage);
