import React, { useEffect, useState } from "react";
import { bindActionCreators } from "redux";
import { push } from "connected-react-router";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import {
  Chip,
  Collapse,
  makeStyles,
  Paper,
  Theme,
  Typography,
} from "@material-ui/core";
import { FilterList } from "@material-ui/icons";
import { AppActions, AppState } from "../../_types";
import { LearningFilter } from "./LearningFilter";
import { requestBank } from "../../_actions";
import { IPage } from ".";

const useStyles = makeStyles((theme: Theme) => ({
  header: {
    position: "relative",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  chips: {
    "& > *": {
      margin: theme.spacing(0.5),
    },
  },
  paper: {
    margin: theme.spacing(1),
    height: theme.spacing(8),
    display: "flex",
    justifyContent: "space-evenly",
  },
}));

interface query {
  [key: string]: string;
}
interface IOwnProps {
  page: IPage;
}

type Props = ReturnType<typeof mapDispatchToProps> &
  ReturnType<typeof mapStateToProps>;

const LearningHeader: React.FC<Props> = (props) => {
  const classes = useStyles();
  const { page, filterList, query, push, requestBank } = props;

  const [isFilter, setIsFilter] = useState(true);

  let selectedFilter: query = query;

  const filterClick = () => {
    setIsFilter((prev) => !prev);
  };

  const filterDelete = (key: string) => {
    delete selectedFilter[key];
    pushLink();
  };

  const handleChange = (
    event: React.ChangeEvent<{ name?: string; value: unknown }>
  ) => {
    const value = event.target.value as string;
    const name = event.target.name;
    if (name) {
      if (value) selectedFilter = { ...selectedFilter, [name]: value };
      else filterDelete(name);
    }
    pushLink();
  };

  const pushLink = () => {
    let link = "?";
    Object.keys(selectedFilter).map(
      (key) => key && (link += key + "=" + selectedFilter[key] + "&")
    );
    push(link);
  };

  useEffect(() => {
    requestBank(page.name);
  }, [page, requestBank]);

  const filters = filterList[page.name];
  return (
    <>
      <div className={classes.header}>
        <Typography variant="h3">{page.title}</Typography>
        <div className={classes.chips}>
          {Object.keys(selectedFilter).map(
            (item: string) =>
              item && (
                <Chip
                  onDelete={() => filterDelete(item)}
                  key={item}
                  color="secondary"
                  label={decodeURI(selectedFilter[item])}
                />
              )
          )}
          <Chip
            icon={<FilterList />}
            color={isFilter ? "secondary" : undefined}
            label="فیلتر"
            onClick={filterClick}
          />
        </div>
      </div>
      <Collapse in={isFilter}>
        <Paper elevation={4} className={classes.paper}>
          {Object.keys(filters).map((input) => (
            <LearningFilter
              key={input}
              input={input}
              data={filters[input]}
              value={query[input]}
              handleChange={handleChange}
            />
          ))}
        </Paper>
      </Collapse>
    </>
  );
};
const mapStateToProps = (state: AppState, ownState: IOwnProps) => ({
  page: ownState.page,
  // @ts-ignore
  query: state.router.location.query,
  filterList: state.search.filterList,
});
const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  requestBank: bindActionCreators(requestBank, dispatch),
  push: bindActionCreators(push, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LearningHeader);
