// @ts-nocheck
import React, { useEffect, useState } from "react";
import {
  CircularProgress,
  IconButton,
  makeStyles,
  Theme,
} from "@material-ui/core";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import { Favorite, FavoriteBorder } from "@material-ui/icons";
import { AppActions, AppState } from "../../../_types";
import { addFavorites } from "../../../_actions/profile.actions";
import { IFavorite } from "../../../_types/profile.types";

const useStyles = makeStyles((theme: Theme) => ({
  rightChip: {
    backgroundColor: theme.palette.background.default,
    position: "absolute",
    zIndex: 30,
    width: 34,
    height: 34,
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
  svgSize: {
    color: theme.palette.secondary.main,
    width: 24,
    height: 24,
    "&:hover": {
      color: theme.palette.primary.main,
    },
  },
  loading: {
    maxWidth: 24,
    maxHeight: 24,
    color: theme.palette.secondary.main,
  },
}));

interface CardFavoriteProps {
  data: IFavorite;
}
type Props = CardFavoriteProps &
  ReturnType<typeof mapStateToProps> &
  ReturnType<typeof mapDispatchToProps>;

export const CardFavorite: React.FC<Props> = ({ data, user, addFavorites }) => {
  const classes = useStyles();
  const [changed, setChanged] = useState(false);
  const { title, thumbnails, description, teacher, id, type } = data;
  const favorite = user.favorite?.some((fav) => fav.id === id);
  const handleFavorite = () => {
    addFavorites({ title, thumbnails, description, teacher, id, type });
  };

  useEffect(() => {
    if (!user.loading) {
      setChanged(false);
    }
  }, [user]);

  const changeFavorite = () => {
    setChanged(true);
    handleFavorite();
  };

  return (
    <IconButton
      size="small"
      aria-label="add to favorites"
      className={classes.rightChip}
      onClick={changeFavorite}
    >
      {changed ? (
        <CircularProgress className={classes.loading} />
      ) : favorite ? (
        <Favorite className={classes.svgSize} />
      ) : (
        <FavoriteBorder className={classes.svgSize} />
      )}
    </IconButton>
  );
};
const mapStateToProps = (state: AppState) => ({
  user: state.user,
});
const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  addFavorites: bindActionCreators(addFavorites, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(CardFavorite);
