import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { push } from "connected-react-router";
import {
  Card,
  Button,
  CardMedia,
  Typography,
  CardContent,
  CardActions,
  CardActionArea,
} from "@material-ui/core";
import { makeStyles, Theme } from "@material-ui/core/styles";

import { thumbnailsFinder } from "../../_helpers/constants";
import CardFavorite from "../Layout/Inputs/CardFavorite";
import { ICardDetail, AppActions } from "../../_types";
import { CardChip } from "../Layout/Inputs/CardChip";
import { IPage } from ".";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: 300,
    maxHeight: 360,
    position: "relative",
    margin: theme.spacing(2),
    [theme.breakpoints.down(350)]: {
      margin: theme.spacing(1),
    },
  },
  cardAction: {
    justifyContent: "space-between",
    margin: "inherit",
  },
  content: {
    minHeight: 150,
  },
}));
type ILessonCard = {
  lesson: ICardDetail;
  page: IPage;
};
type Props = ILessonCard & ReturnType<typeof mapDispatchToProps>;

export const LessonCard: React.FC<Props> = ({ lesson, page, push }) => {
  const classes = useStyles();
  const { title, thumbnails, description, teacher, id, info } = lesson;
  const { type, link } = page;

  const clickHandler = () => {
    push(link + "/" + id);
  };
  return (
    <Card className={classes.root}>
      {type === "exercise" || type === "lesson" ? (
        <CardFavorite data={{ ...lesson, type }} />
      ) : null}
      <CardChip labels={info} />
      <CardActionArea onClick={clickHandler}>
        <CardMedia
          component="img"
          alt={title}
          height="140"
          image={thumbnailsFinder(thumbnails)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
          از {teacher}
        </Typography>
        <Button size="medium" color="secondary" onClick={clickHandler}>
          ادامه درس
        </Button>
      </CardActions>
    </Card>
  );
};
const mapStateToProps = () => ({});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  push: bindActionCreators(push, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LessonCard);
