import React from "react";
import { Grid, makeStyles, Theme, Typography } from "@material-ui/core";
import { IPage } from ".";
import { connect } from "react-redux";
import LessonCard from "./LearningCard";
import { LoadingCircle } from "../Layout/Loadings";
import { AppState, LessonInfo2 } from "../../_types";

const useStyles = makeStyles((theme: Theme) => ({
  cardList: {
    minHeight: "calc(100vh - 240px)",
  },

  autoMargin: {
    margin: "auto",
  },
}));

interface LearningBodyProps {
  page: IPage;
}

type Props = LearningBodyProps & ReturnType<typeof mapStateToProps>;

const LearningBody: React.FC<Props> = ({ page, bank, query }) => {
  const classes = useStyles();
  const { loading, data, error } = bank[page.name];
  const filteredData = data.filter((lesson) =>
    (Object.keys(query) as Array<keyof LessonInfo2>).every(
      (key) => lesson.info[key]?.name === decodeURI(query[key])
    )
  );
  return (
    <Grid container justify="center" className={classes.cardList}>
      {loading ? (
        <LoadingCircle />
      ) : filteredData.length ? (
        filteredData.map((lesson: any) => (
          <LessonCard key={lesson.id} lesson={lesson} page={page} />
        ))
      ) : (
        <Typography component="div" className={classes.autoMargin}>
          {error}
        </Typography>
      )}
    </Grid>
  );
};
const mapStateToProps = (state: AppState) => {
  // @ts-ignore
  const query: { [key: string]: string } = state.router.location.query;
  Object.keys(query).forEach(
    (key) => query[key] === undefined && delete query[key]
  );
  return {
    bank: state.bank,
    query,
  };
};
export default connect(mapStateToProps)(LearningBody);
