import {
  FormControl,
  InputLabel,
  makeStyles,
  MenuItem,
  Select,
  Theme,
} from "@material-ui/core";
import React from "react";

const useStyles = makeStyles((theme: Theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 80,
  },
}));

interface FilterSelectProps {
  handleChange: (
    event: React.ChangeEvent<{ name?: string; value: unknown }>
  ) => void;
  value?: any;
  input: string;
  data: (string | number)[];
}

export const LearningFilter: React.FC<FilterSelectProps> = ({
  input,
  value,
  data,
  handleChange,
}) => {
  const classes = useStyles();
  return (
    <FormControl className={classes.formControl}>
      <InputLabel color="secondary" shrink>
        {input}
      </InputLabel>
      <Select
        color="secondary"
        name={input}
        value={value ? decodeURI(value) : ""}
        onChange={handleChange}
        displayEmpty
      >
        <MenuItem value={""}>هیچ</MenuItem>
        {data.map((d, i) => (
          <MenuItem key={i} value={d}>
            {d}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};
