import { Dispatch } from "react";
import { API } from "../_helpers/api";
import { getTheTime } from "../_helpers/constants";
import { FILTERLIST, IQuery } from "../_types/search.types";
import { AppActions, AppState, BankPages } from "../_types";
import { errorHandler } from "../components/Layout/SnackBar/alert";

export const requestBank = (page: keyof typeof BankPages) => (
  dispatch: Dispatch<AppActions>,
  getState: () => AppState
) => {
  const bankState = getState().bank[page];
  const currentTime = getTheTime();
  const resetTime = currentTime - bankState.nextLoad;
  if (!bankState.data || resetTime >= 0) {
    dispatch({ type: BankPages[page].request });
    API.get(BankPages[page].api)
      .then((res) => {
        dispatch({
          type: FILTERLIST,
          payload: filters(page, res.data),
        });
        dispatch({
          type: BankPages[page].success,
          payload: {
            data: res.data,
            nextLoad: getTheTime(10),
          },
        });
      })
      .catch((err) => errorHandler(err, BankPages[page].failure));
  }
};

const filters = (page: keyof typeof BankPages, data: any[]) => {
  let filter = { [page]: {} } as IQuery;
  data.forEach((d) => {
    Object.keys(d.info).forEach((f) => {
      if (!filter[page][f]) filter[page][f] = [];
      if (filter[page][f].indexOf(d.info[f].name) === -1)
        filter[page][f].push(d.info[f].name);
    });
  });
  return filter;
};
